<template>
	<div class="user_invitation">
		<div class="invitation_wrap">
			<div>
				<h1>会员奖励计划</h1>
        <ul>
          <li>
            刷手：<br />
            0-200      ￥3<br />
            200-500    ￥3<br />
            500-1000   ￥3<br />
            1000-2000  ￥3<br />
            2000+      ￥3
          </li>
          <li>一级好友：￥ 6 </li>
          <li>二级好友：￥ 3</li>
        </ul>

        <van-cell-group style="margin-top: 60px;">
          <van-field v-model="mobile" placeholder="请输入好友手机号" />
        </van-cell-group>
			</div>
		</div>
    <van-button @click="getQRCode" class="bottom_btn" type="primary"  bottomAction>立即分享,邀请朋友</van-button>
    <van-dialog
      v-model="show"
      title="长按保存图片"
      :showCancelButton="false"
      :showConfirmButton="false"
      :closeOnClickOverlay="true"
      style="text-align: center">
      <img ref="qrcode" src="" style="margin:20px; width: 200px; height: 200px;">
    </van-dialog>
	</div>
</template>

<script>

  import { Dialog,Toast } from 'vant';
  import QRCode from 'qrcode';
  // import { NOTICES } from "../../../api/user";

  Vue.use(Dialog);

	export default {
	  data() {
	    return{
	      show: false,
        mobile:''
      }
    },
    components : {
      [Dialog.name]: Dialog,
      Toast
    },
    methods:{
      getQRCode(){
        if(!this.mobile){
          Toast.fail("请填写朋友手机号码");
          return;
        }
        let account = localStorage.getItem('account');
        let friend_account = this.mobile;
        var opts = {
          errorCorrectionLevel: 'H',
          type: 'image/jpeg',
          rendererOpts: {
            quality: 0.3
          }
        }
        let text =  'http://sd.ezmate.cn/client/#/login/reg/' + account + '/' + friend_account;
        let _this = this;
        QRCode.toDataURL(text, opts, function (err, url) {
          if (err) throw err
          _this.$refs.qrcode.src = url;
          _this.show = true;
        })
        // this.$reqGet(NOTICES, {
        //   friend_account: this.mobile
        // }).then(res => {
        //   if(!res.data || res.data.code != 0){
        //     let msg = (res.data && res.data.msg) ? res.data.msg : "获取分享二维码失败";
        //     Toast.fail(msg);
        //     return;
        //   }
        //   this.$refs.qrcode.src = res.data.qr_code;
        //   this.show = true;
        // });
      }
    }
	}
</script>

<style lang="scss" scoped>
	.invitation_wrap{
		height: 90%;
		box-sizing: border-box;
		padding: 30px 20px 0 20px;
		>div{
			height: 100%;
			background-color: #fff;
			padding: 30px 20px;
			box-sizing: border-box;
			overflow-x: hidden;
			overflow-y: auto;
		}
		h1{
			margin: 0;
			font-weight: normal;
			font-size: 20px;
			text-align: center;
			margin-bottom: 10px;
		}
    ul{
      list-style-type:square;
      margin-left:30px;
    }
		li{
			margin: 0;
			line-height: 22px;
		}
	}
	.bottom_btn{
		position: fixed;
		bottom: 0;
	}
</style>



