<template>
  <div class="tab_home">

    <!-- <form action="/search">
      <van-search
        placeholder="请输入好友名称"
        v-model="searchVal"
        @search="resetInit"
        showAction/>
    </form> -->

    <van-tabs @click="handleTabClick" @disabled="handleTabClick">
      <van-tab
        v-for="tab in tabsItem"
        :title="tab.name"
        :key="tab.type">
      </van-tab>
    </van-tabs>

    <van-list
      v-model="loading"
      :finished="finished"
      :immediate-check="false"
      :offset="100"
      @load="loadMore"
    >
      <van-card
        v-for="(item, i) in items"
        :title="item.friend_name"
        :price="item.gainMoney"
        :thumb="item.friend_portrain ? item.friend_portrain : defaultPic"
        :key="i">
        <div slot="desc" class="van-card__row">
          <div class="van-card__desc">{{item.friend_account}}</div>
        </div>
        <div slot="footer" class="footer">
          注册时间：{{item.createTime}}
        </div>
      </van-card>

    </van-list>

    <is-empty v-if="isEmpty">暂无好友</is-empty>

  </div>
</template>

<script>
  import { FRIENDS } from "@/api/user";
  import IsEmpty from "@/vue/components/is-empty/";
  import { Search, Tab, Tabs, Card, Toast } from "vant";
  import loadMore from "@/vue/mixin/list-load-more";

  export default {
    name: "friend-list",
    mixins: [loadMore],
    data() {
      return {
        defaultPic: require(`@/assets/images/head.png`),
        friend_level : 0,
        isEmpty: false,
        tabsItem: [
          { name: "徒弟", index: 1 },
          { name: "徒孙", index: 2 }
        ],
        searchVal: ""
      };
    },

    created() {
      this.resetInit();
    },

    methods: {
      initData(loadMore = false) {
        return this.$reqGet(FRIENDS, {
          key: this.searchVal,
          friend_level: this.friend_level,
          per_page: this.pages.perPage,
          page: this.pages.currPage
        },{
          hideLoading: true
        }).then(res => {
          return this.loadSuccess(res);
        })
      },
      loadSuccess(res){
        let list = res.data.data.list;
        if(!list){
          setTimeout(() => {
						this.isEmpty = true
					}, 500)
          return this.pages;
        }
        let page = {
          "pageCount":res.data.data.pageCount,
          "currPage":res.data.data.currPage,
          "perPage":res.data.data.perPage,
          "totalCount":res.data.data.totalCount
        };
        this.items.push(...list);
        return page;
      },

      handleTabClick(i) {
        let friend_level = this.tabsItem[i].index;
        if (friend_level !== this.friend_level) {
          this.friend_level = friend_level;
          this.resetInit();
        }
      }
    },

    components: {
      [Card.name]: Card,
      [Tab.name]: Tab,
      [Tabs.name]: Tabs,
      [Search.name]: Search,
      [IsEmpty.name]: IsEmpty,
      [Toast.name]: Toast
    }
  };
</script>

<style lang="scss" scoped>
.footer{
  text-align: right;
  font-size: 12px;
  margin-bottom: 2px;
}
</style>



